import { SELECTORS } from '$utils/constants';

import { StartingPrice } from '../utils/StartingPrice';

window.Webflow ||= [];
window.Webflow.push(async () => {
  const startingPriceList = document.querySelector<HTMLDivElement>(SELECTORS.startingPriceList);
  if (startingPriceList) {
    const startingPrice = new StartingPrice(startingPriceList);
    await startingPrice.getHotelStartingPrice();
  }
});
